import { ApiProperty, ApiResponse, ApiResponseProperty } from '@nestjs/swagger';
import { IsNotEmpty } from 'class-validator';

export class CreateMaterialArtDto {
  @IsNotEmpty()
  @ApiProperty({
    example: 'Angular Thn Cng',
    default: 'Angular Thn Cng',
  })
  name!: string;

  @ApiProperty({
    example: 'M t mn v hc',
    default: 'Description',
  })
  description!: string;

  @IsNotEmpty()
  @ApiProperty({
    example: 'Thng tin Id ca mn phi to ra mn v hc ny.',
    default: 'xxxx',
  })
  sectId!: string;

  @IsNotEmpty()
  @ApiProperty({
    example: 'M t hnh nh ca b mn v cng',
    default: 'url',
  })
  logo!: string;
}
